@import url(https://fonts.googleapis.com/css2?family=McLaren&family=Montserrat:ital,wght@0,100;0,300;0,500;0,600;1,100;1,300;1,500;1,600&display=swap);
.contact{
    background-color: white;
    display: flex;
}

.contact .left{
    flex: 1 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.contact .left img{
    height: 90%;
    overflow: hidden;
    
}

.contact .right{
    flex: 1 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.contact .right h2{
    font-size: 35px;
    margin-top: 20px;
    margin-bottom: 20px;
}

.contact .right .links{
    width: 300px;
    display: flex;
    align-items: inherit;
    justify-content: space-evenly;
}

.contact .right .links a{
    display:flex;
    flex-direction: column;
    align-items: inherit;
    text-decoration: none;
    color: black;
    margin: 15px;
}

.contact .right .links a img{
    cursor: pointer;
    height: 40px;
    
}


.contact .right .links button h3{
    font-size: 20px;
}

.contact .right form{
    width: 70%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content:flex-start;
    margin-top: 30px;
}

.contact .right form input{
    width: 300px;
    height: 30px;
    font-size: 14px;
    margin: 10px;
}

.contact .right form textarea{
    width: 300px;
    height: 200px;
    font-size: 14px;
    margin: 10px;
}

.contact .right form button{
    width: 150px;
    height: 40px;
    background-color: lightblue;
    border: none;
    font-size:17px;
    border-radius: 10px;
    font-weight:bolder;
    cursor:pointer;
    margin: 10px;

}

.intro{
    background-color: white;
    display: flex;
}

.intro .left{
    flex: 0.55 1;
    background-color: white;
    overflow: hidden;
}

.intro .right{
    flex: 0.45 1;
    position: relative;
}

.logo{
    text-decoration: none;
    color: black;
}

.intro .left .imgContainer{
    width: 700px;
    height: 700px;
    align-items: center;
    display: flex;
    margin: 20px;
    margin-left: 40px;
    justify-content: center;
}

.intro .left .imgContainer img{
    width: 100%;
    height: 100%;
    
}

.intro .right a{
    text-decoration: none;
    color: black;
    position: absolute;
    bottom: 10px;
    font-size: 50px;
    left: 40%;
    animation: arrowBlink 3s infinite;
}

@keyframes arrowBlink{
    to{
        opacity: 0;
    }
}

.intro .right .wrapper{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 50px;
    position: absolute;
    bottom: 0px;
}

.intro .right .wrapper .stationary{

}

.intro .right .wrapper h1{
    font-size: 70px;
}
.intro .right .wrapper h2{
    font-size: 30px;
}
.intro .right .wrapper h3{
    font-size: 30px;
    
}

.intro .right .wrapper h3 span{
    color: #FE0222;
}
.projects{
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.projects h1{
    font-size: 40px;
    margin-top: 20px;
    margin-bottom: 10px;
}

.projects ul{
    color: black;
    margin: 10px;
    
    padding: 0;
    list-style: none;
    display: flex;
}

.projects .container{
    width: 80%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    margin: 10px;
}

.projects .container ul{
    display: flex;
    margin: 0;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    text-align: center;
}

.projects .container .item{
    margin: 10px 20px;
    width: 320px;
    height: 220px;
    padding: 0;
    
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    z-index: 2;
    flex-wrap: wrap;
    text-align: center;
}

.projects .container .item h3{
    position: absolute;
    color: white;
    margin: 10px;
}

.projects .container .item img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 20px;
    z-index: 2;
}

.projects .container li:hover{
    background-color: black;
    border-radius: 20px;        
}

.projects .container .item img:hover{
    opacity: 0.5;
    z-index: 0;
}


.skills{
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.skills h1{
    font-size: 40px;
    margin-top: 20px;
    margin-bottom: 10px;
}

.skills ul{
    color: black;
    margin: 10px;
    
    padding: 0;
    list-style: none;
    display: flex;
}

.skills .container{
    width: 70%;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 30px;
}

.skills .container .item{
    margin: 10px 20px;
    width: 200px;
    height: 200px;
    border: none;
    
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    z-index: 2;
    flex-wrap: wrap;
    text-align: center;
    border-radius: 50%;
    background-color: #EEEEEE;
}

.skills .container .item h3{
    margin: 10px;
    position: absolute;
    color: black;
    font-size: 20px;
}

.skills .container .item img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 50%;
    opacity: 0.2;
}

.skills .container .item img:hover{
    opacity: 0;
    z-index: 0;
}


li{
    font-size: 15px;
    margin-right: 20px;
    margin-left: 20px;
    padding: 8px;
    border-radius: 10px;
    cursor: pointer;
}

ul .active{
    background-color: black;
    color: white;
}
.topbar{
    background-color: #ee2842;
    width: 100%;
    height: 80px;
    position: fixed;
    top: 0;
    z-index: 4;
    transition: all 1s ease;
}

.topbar .wrapper{
    padding: 10px 30px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.topbar .wrapper .left{
    display: flex;
    align-items: center;
}

.topbar .itemContainer{
    display: flex;
    align-items: center;
    margin: 20px;
}

.topbar .itemContainer .icon{
    margin-right: 5px;
    margin-left: 5px;
    font-size: 20px;
}

.topbar .logo{
    font-size: 40px;
    color: inherit;
}

.topbar .phoneNumber{
    text-decoration: none;
    color:inherit;
}

.topbar .mailID{
    text-decoration: none;
    color: inherit;
}

.topbar .right .hamburger{
    width: 32px;
    height: 25px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.topbar .wrapper .right .hamburger span{
    width: 100%;
    height: 3px;
    background-color: black;
    transform-origin: left;
    transition: all 1s ease;
}

.active{
    background-color: black;
    color: #ee2842;
}

.active .wrapper .right .hamburger .line1{
    background-color: #ee2842;
    transform: rotate(45deg);
}

.active .wrapper .right .hamburger .line2{
    opacity: 0;
}

.active .wrapper .right .hamburger .line3{
    background-color: #ee2842;
    transform: rotate(-45deg);
}
.app{
    height: 100vh;
}

.app .sections{
    height: calc(100vh - 80px);
    width: 100%;
    position: relative;
    top: 80px;
    -ms-scroll-snap-type: y mandatory;
        scroll-snap-type: y mandatory;
    overflow-y: scroll; 
    scroll-behavior: smooth;
}

.sections::-webkit-scrollbar{
    display: none;
}

.sections > *{
    height: calc(100vh - 80px);
    width: 100%;
    scroll-snap-align: start;
}
.menu{
    width: 320px;
    height: 100%;
    background-color:rgb(230, 219, 184);
    z-index: 3;
    position: fixed;
    top: 0;
    right: -300px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    transition: all 1s ease;
}

.active{
    right: 0px;
}

.menu ul{
    margin: 0;
    padding: 0;
    list-style: none;
    font-weight: normal;
    color: black;
    width: 65%;
}

.menu ul li{
    margin-bottom: 20px;
    font-size: 25px;
}

.menu ul li:hover{
    font-weight:bolder;
}

ul li a{
    color: inherit;
    text-decoration: none;
}
*{
    font-family: 'McLaren', cursive;
    font-family: 'Montserrat', sans-serif;
    margin: 0;
}
